* {
    box-sizing: border-box;
    margin: 0px;
    padding: 0px;
}

#root {
    height: 100%;
}

html,
body {
    min-height: 100vh;
    height: 100%;
}

span {
    font-size: 40px;
}

.dark-mode {
    background-color: #1a1919;
    color: #999;
    height: 100%;
    transition: background-color 0.25s ease-in-out;
}

.light-mode {
    background-color: #fff;
    color: #333;
    height: 100%;
    transition: background-color 0.25s ease-in-out;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 14px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
}

.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: 0.4s;
    transition: 0.4s;
}

input:checked+.slider {
    background-color: #19a2c1;
}

input:focus+.slider {
    box-shadow: 0 0 1px #2196f3;
}

input:checked+.slider:before {
    -webkit-transform: translateX(26px);
    -ms-transform: translateX(26px);
    transform: translateX(26px);
}


/* Rounded sliders */

.slider.round {
    border-radius: 34px;
}

.slider.round:before {
    border-radius: 50%;
}

.switch-checkbox {
    padding: 10px;
}

.searchbar {
    height: auto;
    margin: 0 auto;
    text-align: center;
    align-items: center;
}

.searchbar input {
    margin: 30px 0;
    padding: 5px 10px;
    border-radius: 5px;
    border-color: #081F37;
    border: 2px solid #081F37;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.06);
    font-family: 'Courier New', Courier, monospace;
}